import ReactEcharts from "echarts-for-react";
import React, { useMemo } from "react";
import { ColumnCenter, Row, RowCenter } from "../styled";
import { HeaderWrap, HeaderItem, HeaderText, Line, SubTitle } from "./styled";
import { Document, Page, pdfjs } from "react-pdf";
import { IMapData } from "./action";
import { MapTable } from "./MapTable";
pdfjs.GlobalWorkerOptions.workerSrc = `//cdnjs.cloudflare.com/ajax/libs/pdf.js/${pdfjs.version}/pdf.worker.min.js`;

export const Map: React.FC<{ mapData: IMapData }> = ({ mapData }) => {
  const mapOption1 = {
    tooltip: {
      trigger: "axis",
      axisPointer: {
        // 坐标轴指示器，坐标轴触发有效
        type: "line", // 默认为直线，可选为：'line' | 'shadow'
      },
    },
    xAxis: {
      type: "category",
      data: mapData.option1X,
    },
    yAxis: {
      type: "value",
    },
    series: [
      {
        data: mapData.option1Y,
        type: "line",
      },
    ],
  };

  const mapHeaderArr = [
    {
      label: "总分",
      value: mapData.rit || "-",
    },
    {
      label: "测试时长(minutes)",
      value: mapData.duration || "-",
    },
    {
      label: "测试日期",
      value: mapData.date || "-",
    },
  ];

  const demoColor = useMemo(
    () => [
      {
        color: "green",
        text: "测试通过",
      },
      {
        color: "yellow",
        text: "需要加强",
      },
      {
        color: "red",
        text: "需要干预",
      },
      {
        color: "gray",
        text: "未测试",
      },
    ],
    []
  );

  return (
    <ColumnCenter>
      <HeaderWrap style={{ marginTop: 10 }}>
        {mapHeaderArr.map((v, i, arr) => (
          <HeaderItem key={i} style={i % 2 === 0 ? {} : { textAlign: "right" }}>
            <span>{v.label}</span>
            <HeaderText style={i === 1 ? { fontWeight: "bolder" } : {}}>
              {v.value}
            </HeaderText>
          </HeaderItem>
        ))}
      </HeaderWrap>
      <Line></Line>
      <div>
        <SubTitle
          style={{
            marginTop: 0,
            color: "#333",
            marginBottom: 0,
            textAlign: "left",
          }}
        >
          总分变化趋势
        </SubTitle>
        <ReactEcharts
          option={
            mapOption1 as echarts.EChartOption<echarts.EChartOption.Series>
          }
          theme="Imooc"
          style={{ height: "350px", width: "1400px" }}
        />
      </div>
      <Line></Line>
      <SubTitle
        style={{
          marginTop: 0,
          color: "#333",
          textAlign: "left",
          width:'1400px'
        }}
      >
        知识点分类得分
      </SubTitle>
      <MapTable
        achievement_above_mean={mapData.achievement_above_mean}
        flesch_kincaid_grade_level={mapData.flesch_kincaid_grade_level}
        growth_goals_date={mapData.growth_goals_date}
        lexile_score={mapData.lexile_score}
        sub_domains_info={mapData.sub_domains_info}
      />
      <Line></Line>
      {mapData.url && (
        <div>
          <div
            style={{
              width: 1400,
              display: "flex",
              justifyContent: "space-between",
            }}
          >
            <SubTitle
              style={{
                marginTop: 0,
                color: "#333",
                marginBottom: 0,
                textAlign: "left",
              }}
            >
              CCSS知识点筛查图谱
            </SubTitle>
            <RowCenter>
              {demoColor.map((v, i) => (
                <RowCenter style={{ margin: "0 8px" }}>
                  <span
                    style={{
                      backgroundColor: v.color,
                      width: 18,
                      height: 18,
                      display: "inline-block",
                      borderRadius: 5,
                      marginRight: 3,
                    }}
                  />
                  {v.text}
                </RowCenter>
              ))}
            </RowCenter>
          </div>
          <Document file={mapData.url}>
            <Page width={1400} pageNumber={1} />
          </Document>
        </div>
      )}
    </ColumnCenter>
  );
};
