import { get, post } from "../utils/rpc";

export interface IRes {
  errorCode: string,
  executed: boolean,
  message: string,
  success: boolean
}


export interface fetchPdfDataRes extends IRes {
  lexile_measure: string,
  scaled_score: number,
  sub_domain_score_trend_date: string[],
  sub_domain_score_trend_value: number[][],
  sub_items_alphabetic_principle: number[],
  sub_items_other_domains:number[]
  sub_items_phonemic_awareness: number[],
  sub_items_phonics1: number[],
  sub_items_phonics2: number[],
  sub_items_structural_vocabulary: number[],
  test_date: string
}


export const fetchPdfData = (phone: string): Promise<fetchPdfDataRes> => {
  return get({
    url: "/scaledscores/" + phone,
  });
};

export interface fetchPdfMapDataRes extends IRes {
  achievement_above_mean: string;
  flesch_kincaid_grade_level: string;
  growth_goals_date: string;
  lexile_score: string;
  sub_domains_info: {
    domain_name: string;
    domain_score: string;
    focus_strength_info: string;
  }[];
  map_pdf_url: string,
  map_score_trend_date: string[],
  map_score_trend_value:string[],
  rit_score: string,
  test_date: string,
  test_duration: string
}

export const fetchPdfMapData = (phone: string): Promise<fetchPdfMapDataRes> => {
  return get({
    url: "/map_stats/" + phone,
  });
};


